<template lang="pug">
form(@submit='saveScholar').group__add-scholar
    label.label__filter Выберите ученика:
    select(v-model="ids.scholarId" required).group__add-scholar-select
        option(:value='0') Выбрать
        option(:value='item.id' v-for="item in filteredScholars") {{item.lastName}} {{item.firstName}} 
    button.save__scholar-button
        .save__scholar
</template> 
<script>
import { mapActions, mapState } from "vuex";
export default { 
    data() {
        return {
            ids: {
                scholarId: 0,
                groupId: this.groupId
            },
            selectedScholarId: 0
        }
    },
    computed: {
        ...mapState ("groupScholars", {
            groupScholars: state => state.groupScholars}),
            filteredScholars: function() {
                return this.scholars
                    .filter(ele=>this.groupScholars.filter(item=>item.id==ele.id).length==0)
                    .filter(ele=>this.groupClass==ele.scholarClass)
            }
    },
    created() {
        this.fetchGroupScholars(this.groupId)
    },
    props: {
        groupId: Number,
        groupClass: Number,
        scholars: Array
    },
    methods: {
        ...mapActions("groupScholars", ["addScholarGroup", "fetchGroupScholars"]),
        saveScholar(){
            if(this.ids.scholarId>0){
                this.addScholarGroup(this.ids)
                this.$emit('saveScholar')
            }
        }
    }
}
</script>
<style scoped lang="scss">
.group__add-scholar {
    display: flex;
    align-items: center;
}
.group__add-scholar-select {
    border-radius: 4px;
    height: 1.75rem;
    background: #FFFAF0;
    margin-right: 5px;
}
.save__scholar-button{
    border-radius: 5px;
    opacity: .7;
    padding: 0;
    &:hover {
        opacity: .5;
        transition: .5s;
        cursor: pointer;
    }
}
.label__filter {
    text-align: right;
}
.save__scholar {
    background: #d0d2d3;
    background-image: url(../../../assets/images/icons/save-scholar.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 20px;
    height: 20px;
    border-radius: 5px;
}
</style>